import React, { Component } from 'react'
import {Link,NavLink} from "react-router-dom"
export default class Login extends Component {
    constructor(){
        super()
        this.state={
            user:{
                name:"",
                pass:""
            }
        }
    }
    changeUser(e,key){
        this.setState({
            user:{
                ...this.state.user,
                [key]:e.target.value
            }
        })
    }
    login(){
       //admin 123
       if(this.state.user.name==="admin"&&this.state.user.pass==="123"){
           sessionStorage.setItem("isLogin",1)
           this.props.history.push("/index")
       }else{
           alert("失败")
       }
    }
    render() {
        const {user}=this.state
        return (
            <div >
               <div>
                   账号：<input type="text" value={user.name} onChange={(e)=>this.changeUser(e,"name")}/>
               </div>
               <div>
                   密码：<input type="text" value={user.pass} onChange={(e)=>this.changeUser(e,"pass")}/>
               </div>
               <button onClick={()=>this.login()}>登录</button>

                {/* <Link to="/index">首页1-link</Link> */}
                {/* <NavLink to="/index">首页2-NavLink</NavLink> */}
            </div>
        )
    }
}
